*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Roboto;
    background-color: #1a1a1c;
    color: #fff;
}
.coin-app{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: #fff;
}
.coin-search{
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.coin-text{
    margin-top: 20px;
    margin-bottom: 32px;
    text-align: center;
}
.coin-input{
    padding-left: 16px;
    width: 300px;
    height: 40px;
    border-radius: 10px;
    border: none;
    background-color: aliceblue;
    color: black;
}
.coin-input::placeholder{
    color: black;
}
